/*
--------------
ConnectionAGO.ts
--------------
Here all the things related with ArcGIS Online (AGO) is handled. One job is to load the feature layer and
read the information of the landmarks. A second part is to load the 3D model of the landmark in the form
of gltf when it is selected.
Lastly there is also a function to load the WebScene for the map part of the application.
*/
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { loadModules } from 'esri-loader';
import { PointData, Helper } from './Helper.js';
export class ConnectionAGO {
    constructor(app) {
        // Note: Lots of hardcoding here, going to change!
        this.url = "https://zurich.maps.arcgis.com/sharing/rest/content/items/"; // Base url for the download of the models
        this.sceneId = null;
        this.featureLayerId = null;
        this.featureLayerRest = null;
        this.gltfLoader = new GLTFLoader(); // To read new gltf's into ts
        this.init = (sceneId) => {
            return new Promise((resolve, reject) => {
                this.readJSON(this.url + sceneId + "/data").then((infoJson) => {
                    for (var i in infoJson["operationalLayers"]) {
                        if (infoJson["operationalLayers"][i]["title"] == "civic-ar") {
                            this.featureLayerId = infoJson["operationalLayers"][i]["itemId"];
                            this.featureLayerRest = infoJson["operationalLayers"][i]["url"];
                            this.sceneId = sceneId;
                        }
                    }
                    if (this.featureLayerId != null) {
                        this.readJSON(this.url + this.featureLayerId + "/data").then((symbolsJson) => {
                            this.readJSON(this.featureLayerRest.split("SceneServer/layers/0")[0] + "FeatureServer/0/query?where=OBJECTID%20%3C%201000&outFields=*&f=JSON").then((pointsJson) => {
                                this.readPointsAndSymbols(pointsJson.features, symbolsJson);
                                resolve();
                            });
                        });
                    }
                    else {
                        reject("Feature Layer could not be found!");
                    }
                });
            });
        };
        this.readPointsAndSymbols = (pointsJson, symbolsJson) => {
            for (var i in pointsJson) {
                // Re-order the information by making instance of the class PointData
                this.app.pointData[parseInt(pointsJson[i].attributes.identifier)] = new PointData(parseInt(pointsJson[i].attributes.identifier), pointsJson[i].attributes.title, pointsJson[i].attributes.text, pointsJson[i].attributes.type, pointsJson[i].geometry.y, pointsJson[i].geometry.x);
            }
            var symbolInfo = symbolsJson["layers"][0]["layerDefinition"]["drawingInfo"]["renderer"]["uniqueValueInfos"];
            for (var i in symbolInfo) {
                if (symbolInfo[i].symbol.type == "styleSymbolReference") {
                    this.app.pointData[parseInt(symbolInfo[i].value)].has3DModel = true;
                    this.app.pointData[parseInt(symbolInfo[i].value)].url3DModel = this.url + this.featureLayerId + "/resources/styles/gltf/resource/" + symbolInfo[i].symbol.name + ".glb";
                }
                else {
                    this.app.pointData[parseInt(symbolInfo[i].value)].has3DModel = false;
                }
            }
        };
        // Function to download one specific model
        this.loadGLTF = (url) => {
            // Download and load it as gltf
            return new Promise((resolve, reject) => {
                this.gltfLoader.load(url, gltf => {
                    resolve(gltf.scene);
                }, function (xhr) {
                    console.log((xhr.loaded / xhr.total * 100) + '% loaded'); // Does not work yet....
                }, function (err) {
                    reject(err);
                });
            });
        };
        // Read any json from the web or local
        this.readJSON = (urlJson) => {
            // Read the json file here
            return new Promise((resolve, reject) => {
                var xhr = new XMLHttpRequest(); // xhr is a local variable
                xhr.responseType = "json"; // Make sure the server returns json
                xhr.open("GET", urlJson); // Open the file
                xhr.send();
                xhr.onreadystatechange = function () {
                    // If there are no errors, everything worked fine
                    if (xhr.readyState == 4 && xhr.status == 200) {
                        let jsonData = xhr.response; // Read the file
                        resolve(jsonData);
                    }
                };
            });
        };
        // Function to initialize the WebScene for the map part of the app
        this.initMap = (id) => {
            // this will lazy load the ArcGIS API
            // and then use Dojo's loader to require the classes
            loadModules(["esri/widgets/Track", 'esri/views/SceneView', 'esri/WebScene'])
                .then(([Track, SceneView, WebScene]) => {
                // load scene with portal ID
                this.scene = new WebScene({
                    portalItem: {
                        id: id
                    },
                });
                // create a view
                this.view = new SceneView({
                    container: "sceneView",
                    map: this.scene,
                });
                var track = new Track({
                    view: this.view,
                    goToLocationEnabled: false // disable this since we want to control what happens after our location is acquired
                });
                this.view.ui.add(track, "top-right");
                // This part loads the attributes of all features from the layer inside of the scene
                var that = this; // so solve the issue that "this" is the view inside the function
                this.view.when(function () {
                    console.log(that.scene);
                    var layer = that.scene.layers.find(function (layer) { return layer.title === "landmarksZurich"; });
                    var query = layer.createQuery();
                    layer
                        .queryFeatures(query)
                        .then((results) => {
                        if (results.features.length > 0) {
                            console.log(results.features);
                        }
                    });
                });
            })
                .catch(err => {
                // handle any errors with loading the arcgis api packages
                console.error(err);
            });
        };
        this.app = app;
        this.helper = new Helper();
    }
}
