/*
--------------
ThreeScene.ts
--------------
This class deals with the display and interaction with the virtual objects. They are all handled by the three.js
package, therefor the name. There are functions to load 3D models, place 3D models or update 3D models.
Also the interaction in 3D in virtual space is handled here. There is a function to click (onSelect) and also
functions to tap and swipe (onSelectStart).
*/
import * as THREE from "three";
//import { GLTF, GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Helper } from './Helper.js';
export class ThreeScene {
    constructor(app) {
        this.spotlight = null; // The landmark gets a special spotlight
        this.turning = false; // Is the landmark object turning right now?
        this.first = true; // Is it the first time turning the object
        this.balloons = [];
        this.labels = [];
        this.gltfLoader = new GLTFLoader(); // Function/Class to load gtlfs
        // Initialize the scene
        this.initScene = (session) => {
            // Create the elements to draw on
            this.canvas = document.createElement('canvas');
            this.gl = this.canvas.getContext('webgl', { xrCompatible: true });
            // New scene and camera
            this.scene = new THREE.Scene();
            this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
            this.camera.matrixAutoUpdate = false; // the camera matrix gets updated in Geoalignment.ts with the movement of the phone
            /*
            var light = new THREE.PointLight(0xffffff, 2, 100); // soft white light
            light.position.z = 1;
            light.position.y = 5;
            this.scene.add(light);
            */
            // Add a light, in this case one light from above and one from the bottom
            const skyColor = 0xffffff; // white
            const groundColor = 0x4d4d4d; // grey
            const intensity = 1;
            const light = new THREE.HemisphereLight(skyColor, groundColor, intensity);
            light.castShadow = true;
            this.scene.add(light);
            //  Initialize the raycaster to see if objects have been clicked
            this.raycaster = new THREE.Raycaster();
            // Create and configure three.js renderer with XR support
            this.renderer = new THREE.WebGLRenderer({
                antialias: true,
                alpha: true,
                preserveDrawingBuffer: true,
                canvas: this.canvas,
                context: this.gl,
            });
            this.renderer.autoClear = true;
            this.renderer.setPixelRatio(window.devicePixelRatio);
            this.renderer.setSize(window.innerWidth, window.innerHeight);
            this.renderer.xr.enabled = true;
            this.renderer.xr.setReferenceSpaceType('local');
            this.renderer.xr.setSession(session);
            /*
            // simple sprite to indicate detected surfaces
            this.reticle = new THREE.Mesh(
                new THREE.RingBufferGeometry(0.15, 0.2, 32).rotateX(-Math.PI / 2),
                new THREE.MeshPhongMaterial({ color: 0x0fff00 })
            );
            // we will update it's matrix later using WebXR hit test pose matrix
            this.reticle.matrixAutoUpdate = false;
            this.reticle.visible = false;
            this.scene.add(this.reticle);
            */
            // Create some fog so that objects far away are less intense
            this.scene.fog = new THREE.Fog(0xFFFFFF, 50, 1000);
            // The controller is to enable click events
            this.controller = this.renderer.xr.getController(0);
            this.controller.addEventListener('select', (event) => this.onSelect(event)); // This is to click on 3D objects
            // Those two is to use the swiping gesture to turn the object:
            this.controller.addEventListener('selectstart', (event) => this.onSelectStart(event));
            this.controller.addEventListener('selectend', (event) => {
                this.turning = false;
                this.first = true;
            });
            this.scene.add(this.controller);
        };
        // Fires everytime I click somewhere on the scene
        this.onSelect = (event) => {
            // get the 2D position of the clicked area
            const pointer = new THREE.Vector2();
            pointer.x = event.data.gamepad.axes[0];
            pointer.y = -event.data.gamepad.axes[1];
            // Update the picking ray with the camera and touched position
            this.raycaster.setFromCamera(pointer, this.camera);
            //scene.add(new THREE.ArrowHelper(raycaster.ray.direction, raycaster.ray.origin, 300, 0xff0000) );
            // Calculate objects intersecting the picking ray
            const intersects = this.raycaster.intersectObjects(this.scene.children, true);
            // In case we intersected with an object
            if (intersects.length > 0) {
                // try to get the name (only works if it is a landmark baloon)
                try {
                    var id = intersects[0].object.name; // Get the identifier
                    // Check if the object is in the point data
                    if (Object.keys(this.app.pointData).indexOf(id) > -1) {
                        this.removeModel(); // Remove old model in case there is one
                        var that = this;
                        // If there is currently no modal visible, show it!
                        if (that.app.modal.style.display == "none") {
                            that.app.showModal(parseInt(id));
                        }
                        else {
                            this.app.hideModal(); // Hide the old modal
                            // Wait 0.5sec and then show the new one. This makes it clearer that a new point was clicked!
                            setTimeout(function () {
                                that.app.showModal(parseInt(id));
                            }, 300);
                        }
                    }
                }
                finally {
                    // Do nothing
                }
            }
        };
        // Remove the landmark Model in case it is being displayed (together with the light)
        this.removeModel = () => {
            if (this.landmarkModel != null) {
                var model3D = this.scene.getObjectByName(this.landmarkModel.name);
                this.scene.remove(model3D);
                this.scene.remove(this.spotlight);
                this.landmarkModel = null;
            }
        };
        // Download and show the current 3D model
        this.showModel = (i, callback) => {
            var id = this.app.pointData[i].id;
            // Download the corresponding model
            this.app.connectionAGO.loadGLTF(this.app.pointData[id].url3DModel).then((model) => {
                this.landmarkModel = model;
                var bearing = this.app.pointData[id].bearing;
                var pos = this.helper.polarToCart2D(bearing - this.helper.toDegrees(this.scene.rotation.y), 3, 0);
                this.landmarkModel.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(this.landmarkModel, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.scene.add(this.landmarkModel);
                // Also add spotlight 10m above the model
                this.spotlight = new THREE.Object3D;
                var sportlight1 = new THREE.SpotLight(0xffffff, 3, 100, Math.PI / 4); // soft white light
                pos = this.helper.polarToCart2D(bearing - this.helper.toDegrees(this.scene.rotation.y), 1, 1);
                sportlight1.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(sportlight1, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.spotlight.add(sportlight1);
                sportlight1.target = this.landmarkModel;
                var sportlight2 = new THREE.SpotLight(0xffffff, 3, 100, Math.PI / 4); // soft white light
                pos = this.helper.polarToCart2D(bearing - this.helper.toDegrees(this.scene.rotation.y), 5, 1);
                sportlight2.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(sportlight2, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.spotlight.add(sportlight2);
                sportlight2.target = this.landmarkModel;
                this.scene.add(this.spotlight);
                callback(); // callbacl everything worked well  : reject function?
            });
        };
        // Detect a swiping gesture started!
        this.onSelectStart = (event) => {
            // get the 2D position of the clicked area
            const pointer = new THREE.Vector2();
            pointer.x = event.data.gamepad.axes[0];
            pointer.y = -event.data.gamepad.axes[1];
            // update the picking ray with the camera and mouse position
            this.raycaster.setFromCamera(pointer, this.camera);
            // calculate objects intersecting the picking ray
            const intersects = this.raycaster.intersectObjects(this.scene.children, true);
            if (intersects.length > 0) {
                // in case the touch event started on the landmark model!
                if (this.landmarkModel && intersects[0].object.parent.parent == this.landmarkModel) {
                    this.rotationY = this.controller.rotation.y;
                    this.turning = true;
                }
            }
        };
        // Is called from the onXRFrame method on ARHandler.ts
        // used to check if the swipe action is still going
        // if yes, keep turning the model according to the controller rotation
        this.checkTurning = () => {
            if (this.turning) {
                var diff = this.controller.rotation.y - this.rotationY;
                // In case this is the first call after the start of the turn: store the initial rotation
                if (this.first) {
                    this.rotationY = this.controller.rotation.y;
                    diff = 0;
                    this.first = false;
                }
                // check the difference
                else {
                    diff = this.controller.rotation.y - this.rotationY;
                }
                // let the model rotate with double speed
                if (!isNaN(diff)) {
                    this.landmarkModel.rotation.y -= diff * 2;
                }
                // store the current rotation for the next frame
                this.rotationY = this.controller.rotation.y;
            }
        };
        // Load the local models for the dev tools and other stuff
        this.loadLocalModels = () => {
            return new Promise((res, rej) => {
                Promise.all([
                    new Promise((resolve, reject) => {
                        // Load the north arrow
                        this.gltfLoader.load('models/Arrow.glb', (gltf) => {
                            this.arrowN = gltf.scene;
                            this.arrowN.scale.set(1, 1, 1);
                            this.arrowN.castShadow = true;
                            this.arrowN.receiveShadow = true;
                            resolve();
                        }, () => { }, (error) => {
                            console.error(error);
                            reject("Model Arrow.glb could not be found");
                        });
                    }),
                    new Promise((resolve, reject) => {
                        // load the east, south and west arrow
                        this.gltfLoader.load('models/Arrow_blue.glb', (gltf) => {
                            var arrow = gltf.scene;
                            arrow.scale.set(1, 1, 1);
                            arrow.castShadow = true;
                            arrow.receiveShadow = true;
                            this.arrowE = arrow.clone();
                            this.arrowS = arrow.clone();
                            this.arrowW = arrow.clone();
                            resolve();
                        }, () => { }, (error) => {
                            console.error(error);
                            reject("Model Arrow.glb could not be found");
                        });
                    })
                ])
                    .then(() => { res(); })
                    .catch((message) => { rej(message); });
            });
        };
        // Load one model from the internet with url
        this.loadModel = (url) => {
            return new Promise(resolve => {
                this.gltfLoader.load(url, resolve);
            });
        };
        this.placeBalloons2 = () => {
            for (var j in this.balloons) {
                this.scene.remove(this.balloons[j]);
            }
            for (var j in this.labels) {
                this.scene.remove(this.labels[j]);
            }
            var maxDistance = parseInt(document.getElementById('distSlider').value);
            if (maxDistance == 3000) {
                maxDistance = Number.POSITIVE_INFINITY;
            }
            this.gltfLoader.load('models/balloon.glb', (gltf) => {
                var root = gltf.scene;
                // Add a baloon for each landmark
                for (var i in this.app.pointData) {
                    let bearing = this.app.pointData[i].bearing;
                    let distance = this.app.pointData[i].distance;
                    if (distance < maxDistance) {
                        var balloon = root.clone().children[0];
                        balloon.castShadow = true;
                        balloon.receiveShadow = true;
                        balloon.scale.set(10, 10, 10);
                        balloon.material = new THREE.MeshPhongMaterial({ color: this.app.colors[this.app.pointData[i].category], opacity: this.helper.mapTransparency(distance), transparent: true });
                        var pos = this.helper.polarToCart2D(bearing, this.helper.mapDistance(distance), this.helper.mapHeight(distance));
                        balloon.position.set(pos.x, pos.y, pos.z);
                        ; // Add attribute info
                        balloon.userData = { "name": i.toString() };
                        balloon.name = i.toString();
                        this.scene.add(balloon);
                        this.balloons.push(balloon);
                        // In case it is closer then 300m, also give label to the baloons
                        if (distance < 300) {
                            const canvas = this.makeLabelCanvas(100, 32, this.app.pointData[i].name);
                            const texture = new THREE.CanvasTexture(canvas);
                            // because our canvas is likely not a power of 2
                            // in both dimensions set the filtering appropriately.
                            texture.minFilter = THREE.LinearFilter;
                            texture.wrapS = THREE.ClampToEdgeWrapping;
                            texture.wrapT = THREE.ClampToEdgeWrapping;
                            const labelMaterial = new THREE.SpriteMaterial({
                                map: texture,
                                transparent: true,
                            });
                            const labelBaseScale = 0.1;
                            const label = new THREE.Sprite(labelMaterial);
                            var pos = this.helper.polarToCart2D(bearing, this.helper.mapDistance(distance), this.helper.mapHeight(distance) - 25);
                            label.position.set(pos.x, pos.y, pos.z);
                            label.scale.x = canvas.width * labelBaseScale;
                            label.scale.y = canvas.height * labelBaseScale;
                            label.name = i.toString() + "_label";
                            this.scene.add(label);
                            this.labels.push(label);
                        }
                    }
                }
            });
        };
        this.makeBalloonsTransparent = (id) => {
            for (var j in this.balloons) {
                if (this.balloons[j].name != id) {
                    this.balloons[j].material.opacity = 0.2;
                }
            }
            for (var j in this.labels) {
                if (this.labels[j].name != id + "_label") {
                    this.labels[j].material.opacity = 0.2;
                }
            }
        };
        this.makeBalloonsNormal = () => {
            for (var j in this.balloons) {
                let distance = this.app.pointData[this.balloons[j].name].distance;
                this.balloons[j].material.opacity = this.helper.mapTransparency(distance);
            }
            for (var j in this.labels) {
                this.labels[j].material.opacity = 1;
            }
        };
        this.updateBalloons = () => {
            for (var i in this.app.pointData) {
                let bearing = this.app.pointData[i].bearing;
                let distance = this.app.pointData[i].distance;
                var balloon = this.scene.getObjectByName(i.toString());
                var pos = this.helper.polarToCart2D(bearing - this.helper.toDegrees(this.scene.rotation.y), this.helper.mapDistance(distance), this.helper.mapHeight(distance));
                balloon.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(balloon, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                var label = this.scene.getObjectByName(i.toString() + "_label");
                if (label != null) {
                    if (distance > 300) {
                        this.scene.remove(label);
                    }
                    else {
                        var pos = this.helper.polarToCart2D(bearing - this.helper.toDegrees(this.scene.rotation.y), this.helper.mapDistance(distance) - 12, this.helper.mapHeight(distance));
                        label.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                        this.helper.rotateAboutPoint(label, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                    }
                }
                else {
                    if (distance < 300) {
                        const canvas = this.makeLabelCanvas(100, 32, this.app.pointData[i].name);
                        const texture = new THREE.CanvasTexture(canvas);
                        // because our canvas is likely not a power of 2
                        // in both dimensions set the filtering appropriately.
                        texture.minFilter = THREE.LinearFilter;
                        texture.wrapS = THREE.ClampToEdgeWrapping;
                        texture.wrapT = THREE.ClampToEdgeWrapping;
                        const labelMaterial = new THREE.SpriteMaterial({
                            map: texture,
                            transparent: true,
                        });
                        const labelBaseScale = 0.1;
                        const label = new THREE.Sprite(labelMaterial);
                        var pos = this.helper.polarToCart2D(bearing - this.helper.toDegrees(this.scene.rotation.y), this.helper.mapDistance(distance), this.helper.mapHeight(distance) - 25);
                        label.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                        this.helper.rotateAboutPoint(label, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                        label.scale.x = canvas.width * labelBaseScale;
                        label.scale.y = canvas.height * labelBaseScale;
                        balloon.name = i.toString() + "_label";
                        this.scene.add(label);
                    }
                }
            }
        };
        this.handleCompassArrows = (devMode) => {
            if (devMode) {
                this.scene.add(this.arrowN);
                this.scene.add(this.arrowE);
                this.scene.add(this.arrowS);
                this.scene.add(this.arrowW);
            }
            else {
                this.scene.remove(this.arrowN);
                this.scene.remove(this.arrowE);
                this.scene.remove(this.arrowS);
                this.scene.remove(this.arrowW);
            }
        };
        this.updateCompassArrows = (devMode) => {
            if (devMode) {
                var pos = this.helper.polarToCart2D(0 - this.helper.toDegrees(this.scene.rotation.y), 3, 2); // Position north, 3m away and 2m up
                this.arrowN.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(this.arrowN, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.arrowN.setRotationFromAxisAngle(new THREE.Vector3(0, 1, 0), this.helper.toRadians(0));
                var pos = this.helper.polarToCart2D(90 - this.helper.toDegrees(this.scene.rotation.y), 3, 2); // Position east, 3m away and 2m up
                this.arrowE.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(this.arrowE, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.arrowE.setRotationFromAxisAngle(new THREE.Vector3(0, 1, 0), this.helper.toRadians(260));
                var pos = this.helper.polarToCart2D(180 - this.helper.toDegrees(this.scene.rotation.y), 3, 2); // Position south, 3m away and 2m up
                this.arrowS.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(this.arrowS, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.arrowS.setRotationFromAxisAngle(new THREE.Vector3(0, 1, 0), this.helper.toRadians(180));
                var pos = this.helper.polarToCart2D(270 - this.helper.toDegrees(this.scene.rotation.y), 3, 2); // Position west, 3m away and 2m up
                this.arrowW.position.set(this.app.ARHandler.positionLocal[0] + pos.x, this.app.ARHandler.positionLocal[1] + pos.y, this.app.ARHandler.positionLocal[2] + pos.z);
                this.helper.rotateAboutPoint(this.arrowW, [0, 0, 0], new THREE.Vector3(0, 1, 0), -this.scene.rotation.y, false);
                this.arrowW.setRotationFromAxisAngle(new THREE.Vector3(0, 1, 0), this.helper.toRadians(90));
            }
        };
        // Copied from the internet
        this.makeLabelCanvas = (baseWidth, size, name) => {
            const borderSize = 2;
            const ctx = document.createElement('canvas').getContext('2d');
            const font = `${size}px bold sans-serif`;
            ctx.font = font;
            // measure how long the name will be
            const textWidth = ctx.measureText(name).width;
            baseWidth = ctx.measureText(name).width;
            const doubleBorderSize = borderSize * 2;
            const width = baseWidth + doubleBorderSize;
            const height = size + doubleBorderSize;
            ctx.canvas.width = width;
            ctx.canvas.height = height;
            // need to set font again after resizing canvas
            ctx.font = font;
            ctx.textBaseline = 'middle';
            ctx.textAlign = 'center';
            ctx.fillStyle = "rgba(98, 95, 99, 0.7)";
            ;
            ctx.fillRect(0, 0, width, height);
            // scale to fit but don't stretch
            const scaleFactor = Math.min(1, baseWidth / textWidth);
            ctx.translate(width / 2, height / 2);
            ctx.scale(scaleFactor, 1);
            ctx.fillStyle = 'white';
            ctx.fillText(name, 0, 0);
            return ctx.canvas;
        };
        this.app = app;
        this.helper = new Helper();
    }
}
