/*
--------------
Application.ts
--------------
This is the main module which holds all strings together. Here all the instances of the other classes
are made. The module does not hold much functionality, but some of the data and just generally acts as
a organizer. Also almost all the click-event-listener are stated here.

The sub-modules are:
ARHandler: Handles all the things related to WebXR (or WebAR in this case).
ConnectionAGO: Handles all the loading of data from ArcGIS Online
Geoalignment: Handles the listeners for orientation and geolocation. But the actual geoalignment is done in the onXRFrame of the class ARHandler
Helper: Includes all the small helpful functions which are used from different classes
ThreeScene: Deals with the display and interaction with the virtual objects
*/
import { ARHandler } from './ARHandler.js';
import { ConnectionAGO } from './ConnectionAGO.js';
import { Geoalignment } from './Geoalignment.js';
import { Helper } from './Helper.js';
import { ThreeScene } from './ThreeScene.js';
//import { ArrowHelper } from "three";
export class Application {
    constructor() {
        // Buttons Footer
        this.homeButton = document.getElementById('homeButton');
        this.mapButton = document.getElementById('mapButton');
        this.walkButton = document.getElementById('walkButton');
        this.settingsButton = document.getElementById('settingsButton');
        this.activeButton = null;
        // Main containers corresponding to the footer buttons
        this.splash = document.getElementById('splash');
        this.home = document.getElementById('home');
        this.homeContainer = document.getElementById('homeContainer');
        this.mapContainer = document.getElementById('mapContainer');
        this.walkContainer = document.getElementById('walkContainer');
        this.settingsContainer = document.getElementById('settingsContainer');
        this.activeContainer = null;
        // Home Container elements
        this.xrButton = document.getElementById('xrButton');
        this.startButton = document.getElementById('startButton');
        // Walk Container elements
        this.modal = document.getElementById('modal');
        this.btnModel = document.getElementById('btnModel');
        // Settings Container elements
        this.distSlider = document.getElementById("distSlider");
        this.btnFakeGeo = document.getElementById("fakeGeo");
        this.btnDevMode = document.getElementById("devMode");
        // Data
        this.pointData = {}; // Stores all the info about the different landmarks
        this.activePoint = null; // Stores which landmark is clicked at
        this.colors = {
            grey: "#f5f6f4",
            dark_grey: "#625f63",
            pink: "#ef2d56",
            Church: "#048a81",
            Monument: "#f19953",
            Architecture: "#2D93AD",
            Other: "#ef2d56"
        };
        //webSceneId: string = "ba2e8db9e06d46acad6bd7f1a741d400";    // Temporal, hardcoded webscene
        this.webSceneId = "f1efb6c07eb44a3bbb1adc0b584017ed"; // Temporal, hardcoded webscene
        // TODO: Print error to GUI in case the promises don't get resolved
        this.init = () => {
            // Get read the data, check if AR is possible on this device and get all the permissions 
            Promise.all([
                this.connectionAGO.init(this.webSceneId),
                this.threeScene.loadLocalModels(),
                this.ARHandler.checkXR(),
                this.geoalignment.init()
            ]).then(() => {
                this.initHome(); // When all is loaded, remove splash screen and show home container
            }).catch((message) => {
                alert(message);
            });
            // Add all the different event listeners for the buttons
            var that = this;
            // Footer button for settings
            this.settingsButton.addEventListener('click', function () {
                that.activeButton.style.color = "";
                that.activeContainer.style.display = "none";
                that.settingsContainer.style.display = "flex";
                that.activeContainer = that.settingsContainer;
                that.activeButton = that.settingsButton;
                that.activeButton.style.color = that.colors.pink;
            });
            // Footer button for the map (WebScene)
            this.mapButton.addEventListener('click', function () {
                that.activeButton.style.color = "";
                that.activeContainer.style.display = "none";
                that.mapContainer.style.display = "flex";
                that.activeContainer = that.mapContainer;
                that.activeButton = that.mapButton;
                that.activeButton.style.color = that.colors.pink;
            });
            // Footer button for AR Mode (Walk Mode)
            this.walkButton.addEventListener('click', function () {
                that.activeButton.style.color = "";
                that.activeContainer.style.display = "none";
                that.walkContainer.style.display = "flex";
                that.activeContainer = that.walkContainer;
                that.activeButton = that.walkButton;
                that.activeButton.style.color = that.colors.pink;
            });
            // Footer button for the home screen
            this.homeButton.addEventListener('click', function () {
                that.activeButton.style.color = "";
                that.activeContainer.style.display = "none";
                that.homeContainer.style.display = "flex";
                that.activeContainer = that.homeContainer;
                that.activeButton = that.homeButton;
                that.activeButton.style.color = that.colors.pink;
            });
            // The main button to start the AR experience
            this.xrButton.addEventListener('click', function () {
                that.splash.style.display = "flex"; // Remove splash screen
                that.home.style.display = "none"; // Show home screen
                that.ARHandler.enterAr();
            });
            // Settings buttons (switches)
            // Fake the geolocation in order to be in the middle of the city
            this.distSlider.addEventListener('input', function (event) {
                document.getElementById('distValue').innerHTML = event.target.value.toString() + "m";
                if (parseInt(event.target.value) == 3000) {
                    document.getElementById('distValue').innerHTML = "Inf";
                }
            });
            this.distSlider.addEventListener('change', function (event) {
                that.threeScene.placeBalloons2();
            });
            this.btnFakeGeo.addEventListener('change', function () {
                that.ARHandler.xrSession.end();
            });
            // Turn on Developer mode, just gives more information about the coordinate systems and nort/west/south/east
            this.btnDevMode.addEventListener('change', function () {
                if (that.btnDevMode.checked) {
                    document.getElementById('devModeInfo').style.display = "block";
                    that.threeScene.handleCompassArrows(true);
                }
                else {
                    document.getElementById('devModeInfo').style.display = "none";
                    that.threeScene.handleCompassArrows(false);
                }
            });
            // Close the modal
            document.getElementById('close').addEventListener('click', function () {
                that.hideModal();
            });
            // Show or hide the 3D model
            this.btnModel.addEventListener('click', function () {
                if (that.threeScene.landmarkModel == null) {
                    document.getElementById('loaderBars').classList.add("is-active");
                    that.threeScene.showModel(that.activePoint, function () {
                        document.getElementById('loaderBars').classList.remove("is-active");
                    });
                    that.btnModel.innerHTML = "Hide 3D Model";
                }
                else {
                    that.threeScene.removeModel();
                    that.btnModel.innerHTML = "Show 3D Model";
                }
            });
        };
        /*
            // Reads the landmark info from AGO
            readPointData = () => {
                return new Promise<void>((resolve, reject) => {
                    this.connectionAGO.readJSON().then((jsonData: any) => {
                        for (var i in jsonData) {
                            // Re-order the information by making instance of the class PointData
                            this.pointData.push(new PointData(jsonData[i].attributes.identifier, jsonData[i].attributes.titleTeaser, jsonData[i].attributes.textTeaser, jsonData[i].attributes.category, jsonData[i].geometry.y, jsonData[i].geometry.x));
                        }
                        resolve();
                    })
                        .catch(() => {
                            console.error("Point Data could not be processed");
                            reject();
                        })
                });
            }
            */
        // If everything is loaded correctly, start the app
        this.initHome = () => {
            if (this.activeButton) {
                this.activeButton.style.color = "";
                this.activeContainer.style.display = "none";
            }
            this.walkContainer.style.display = "flex";
            // set the active container to the "home" one
            this.activeContainer = this.walkContainer;
            this.activeButton = this.walkButton;
            this.activeButton.style.color = this.colors.pink;
            this.splash.style.display = "none"; // Remove splash screen
            this.home.style.display = "block"; // Show home screen
            document.getElementById('loaderBarsStart').classList.remove("is-active");
            this.ARHandler.enterAr();
            this.connectionAGO.initMap(this.webSceneId); // Start the webscene in the background        
        };
        // When one baloon is clicked, show the modal on the bottom of the screen
        this.showModal = (id) => {
            this.activePoint = id;
            document.getElementById('landmarkInfo').innerHTML = "<b>" + this.pointData[id].name + "</b><br>" + this.pointData[id].description + "<br> Distance to this landmark: " + this.pointData[id].distance.toFixed(0) + "m";
            if (this.pointData[id].has3DModel) {
                this.btnModel.style.display = "block";
            }
            else {
                this.btnModel.style.display = "none";
            }
            this.btnModel.innerHTML = "Show 3D Model";
            this.modal.style.display = "block";
            this.threeScene.makeBalloonsTransparent(id.toString());
        };
        this.hideModal = () => {
            this.modal.style.display = "none";
            this.activePoint = null;
            this.threeScene.removeModel();
            document.getElementById('loaderBars').classList.remove("is-active");
            this.threeScene.makeBalloonsNormal();
        };
        this.ARHandler = new ARHandler(this);
        this.connectionAGO = new ConnectionAGO(this);
        this.threeScene = new ThreeScene(this);
        this.helper = new Helper(this);
        this.geoalignment = new Geoalignment(this);
        var that = this;
        this.startButton.addEventListener('click', function () {
            that.init();
            document.getElementById('loaderBarsStart').classList.add("is-active");
        });
    }
}
