/*
--------------
Helper.ts
--------------
This class includes all the small helpful functions which are used from different classes. For
example conversions from rad to degree, conversion from orientation info to compass heading, rotations
and translations in 3D. Also claculating distances from lat/long information is included.
Additionally there is a small helper class called PointData, which stores all the data for one landmark.
*/
import * as THREE from "three";
//import { ThreeScene } from "./ThreeScene";
export class Helper {
    constructor(app) {
        this.logDiv = document.getElementById('log');
        this.log = (message) => {
            this.logDiv.innerHTML = message + "<br>" + this.logDiv.innerHTML;
            console.log(message);
        };
        this.toggleFullScreen = () => {
            var doc = window.document;
            var docEl = doc.documentElement;
            var requestFullScreen = docEl.requestFullscreen || docEl.mozRequestFullScreen || docEl.webkitRequestFullScreen || docEl.msRequestFullscreen;
            var cancelFullScreen = doc.exitFullscreen || doc.mozCancelFullScreen || doc.webkitExitFullscreen || doc.msExitFullscreen;
            if (!doc.fullscreenElement && !doc.mozFullScreenElement && !doc.webkitFullscreenElement && !doc.msFullscreenElement) {
                requestFullScreen.call(docEl);
            }
            else {
                cancelFullScreen.call(doc);
            }
        };
        // Put an object in front of you, where you are looking right now
        this.placeInFront = (obj, camera, scene) => {
            // Place the object three meters away from you
            var x = camera.getWorldPosition().x + 3 * camera.getWorldDirection().x;
            var y = camera.getWorldPosition().y + 3 * camera.getWorldDirection().y;
            var z = camera.getWorldPosition().z + 3 * camera.getWorldDirection().z;
            obj.position.set(x, y, z);
            // Tricky part: Since the coordinate system is turned to get geoalignment, also consider this rotation!
            this.rotateAboutPoint(obj, [camera.getWorldPosition().x, camera.getWorldPosition().y, camera.getWorldPosition().z], new THREE.Vector3(0, 1, 0), -scene.rotation.y, false);
        };
        // Rotate an object around a certain point/axis about angle theta
        this.rotateAboutPoint = (obj, point, axis, theta, pointIsWorld) => {
            pointIsWorld = (pointIsWorld === undefined) ? false : pointIsWorld;
            if (pointIsWorld) {
                obj.parent.localToWorld(obj.position); // compensate for world coordinate
            }
            var pointVec = new THREE.Vector3;
            pointVec.x = point[0];
            pointVec.y = point[1];
            pointVec.z = point[2];
            obj.position.sub(pointVec); // remove the offset
            obj.position.applyAxisAngle(axis, theta); // rotate the POSITION
            obj.position.add(pointVec); // re-add the offset
            if (pointIsWorld) {
                obj.parent.worldToLocal(obj.position); // undo world coordinates compensation
            }
            obj.rotateOnAxis(axis, theta); // rotate the OBJECT
        };
        this.rotateLabel = (label) => {
        };
        // Take the three angles from the orientation and just take the horizontal part (copied from https://stackoverflow.com/questions/18112729/calculate-compass-heading-from-deviceorientation-event-api)
        this.compassHeading = (alpha, beta, gamma) => {
            // Convert degrees to radians
            var alphaRad = alpha * (Math.PI / 180);
            var betaRad = beta * (Math.PI / 180);
            var gammaRad = gamma * (Math.PI / 180);
            // Calculate equation components
            var cA = Math.cos(alphaRad);
            var sA = Math.sin(alphaRad);
            //var cB: number = Math.cos(betaRad);
            var sB = Math.sin(betaRad);
            var cG = Math.cos(gammaRad);
            var sG = Math.sin(gammaRad);
            // Calculate A, B, C rotation components
            var rA = -cA * sG - sA * sB * cG;
            var rB = -sA * sG + cA * sB * cG;
            //var rC: number = - cB * cG;
            // Calculate compass heading
            var compassHeading = Math.atan(rA / rB);
            // Convert from half unit circle to whole unit circle
            if (rB < 0) {
                compassHeading += Math.PI;
            }
            else if (rA < 0) {
                compassHeading += 2 * Math.PI;
            }
            // Convert radians to degrees
            compassHeading *= 180 / Math.PI;
            return compassHeading;
        };
        // Calculate the haversine distance, distance between two lat/lng coordinates using the great circle (geodesic line)
        this.distance = (lat1, lon1, lat2, lon2, unit) => {
            // Convert to radian
            var radlat1 = Math.PI * lat1 / 180;
            var radlat2 = Math.PI * lat2 / 180;
            var theta = lon1 - lon2;
            var radtheta = Math.PI * theta / 180;
            var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
            dist = Math.acos(dist);
            dist = dist * 180 / Math.PI;
            dist = dist * 60 * 1.1515;
            if (unit == "K") {
                dist = dist * 1.609344;
            } // case kilometer
            if (unit == "N") {
                dist = dist * 0.8684;
            } // case ?
            return dist * 1000;
        };
        this.distanceCart = (pos1, pos2) => {
            return Math.sqrt(Math.pow(pos1[0] - pos2[0], 2) + Math.pow(pos1[1] - pos2[1], 2) + Math.pow(pos1[2] - pos2[2], 2));
        };
        // Converts from degrees to radians.
        this.toRadians = (degrees) => {
            return degrees * Math.PI / 180;
        };
        // Converts from radians to degrees.
        this.toDegrees = (radians) => {
            return radians * 180 / Math.PI;
        };
        // get the bearing (azimuth) between two points
        this.bearing = (startLat, startLng, destLat, destLng) => {
            startLat = this.toRadians(startLat);
            startLng = this.toRadians(startLng);
            destLat = this.toRadians(destLat);
            destLng = this.toRadians(destLng);
            let y = Math.sin(destLng - startLng) * Math.cos(destLat);
            let x = Math.cos(startLat) * Math.sin(destLat) -
                Math.sin(startLat) * Math.cos(destLat) * Math.cos(destLng - startLng);
            let brng = Math.atan2(y, x);
            brng = this.toDegrees(brng);
            return (brng + 360) % 360;
        };
        // ?
        this.polarToCart2D = (angle, radius, height) => {
            let z = -radius * Math.cos(this.toRadians(angle));
            let y = height;
            let x = radius * Math.sin(this.toRadians(angle));
            return { x: x, y: y, z: z };
        };
        // ?
        this.polarToCartOrient = (angle) => {
            let a = angle * Math.PI / 180;
            return { x: 0, y: Math.sin(-a * 0.5), z: 0, w: Math.cos(-a * 0.5) };
        };
        // Check if the device you're using is using iOS
        this.iOS = () => {
            var types = [
                'iPad Simulator',
                'iPhone Simulator',
                'iPod Simulator',
                'iPad',
                'iPhone',
                'iPod'
            ];
            var isIOS = false;
            for (var i in types) {
                if (types[i] == navigator.platform) {
                    isIOS = true;
                }
            }
            return isIOS
                // iPad on iOS 13 detection
                || (navigator.userAgent.includes("Mac") && "ontouchend" in document);
        };
        this.mapDistance = (distance) => {
            return 50 + Math.atan(distance / 1000) * 300;
        };
        this.mapHeight = (distance) => {
            var angle = 20;
            var height = Math.tan(this.toRadians(angle)) * this.mapDistance(distance);
            return Math.max(height, 50);
        };
        this.mapTransparency = (distance) => {
            if (distance < 150) {
                return 1;
            }
            else if (distance > 700) {
                return 0.4;
            }
            else {
                return 1 - 0.6 * (distance - 150) / 550;
            }
        };
        this.app = app;
    }
}
// Small helper class for storing and dealing with the landmarks data
export class PointData {
    constructor(id, name, description, category, lat, lng) {
        this.distance = null; // Calculated distance from current position
        this.bearing = null; // Calculated bearing from current position
        this.has3DModel = null;
        this.url3DModel = null;
        this.closeby = false;
        // Update the distance and bearing from a new current position
        this.updateDist = (position) => {
            this.distance = this.helper.distance(position[1], position[0], this.lat, this.lng, 'K');
            this.bearing = this.helper.bearing(position[1], position[0], this.lat, this.lng);
        };
        this.id = id;
        this.name = name;
        this.description = description;
        this.category = category;
        this.lat = lat;
        this.lng = lng;
        this.helper = new Helper();
    }
}
