import { Helper } from './Helper.js';
import * as THREE from "three";
export class ARHandler {
    constructor(app) {
        this.orientLocal = null; // Stores the current orientation of the phone in the local coordinate system (degree). The global one is stored in Geoalignment.ts
        this.positionLocal = null;
        this.previousPositionLocal = [0, 0, 0];
        // to control the xr session
        this.xrSession = null;
        // reference space used within an application https://developer.mozilla.org/en-US/docs/Web/API/XRSession/requestReferenceSpace
        this.xrRefSpace = null;
        // for hit testing with detected surfaces
        this.xrHitTestSource = null;
        // button to start XR experience
        this.xrButton = document.getElementById('xrButton');
        // HTML elements to write developer info to, only shown when in developer mode
        this.orientLocalVis = document.getElementById('orientLocalVis');
        this.orientGlobalVis = document.getElementById('orientGlobalVis');
        this.diffOrientVis = document.getElementById('diffOrientVis');
        this.localLocVis = document.getElementById('localLocVis');
        // Check if this device can do WebXR
        this.checkXR = () => {
            return new Promise((resolve, reject) => {
                // First pre-condition: work in https!
                if (!window.isSecureContext) {
                    reject("WebXR unavailable. Please use secure context");
                }
                // Secondly: support webxr
                if (navigator.xr) {
                    navigator.xr.addEventListener('devicechange', this.checkSupportedState);
                    navigator.xr.isSessionSupported('immersive-ar').then((supported) => {
                        if (supported) {
                            this.xrButton.innerHTML = 'Exit App';
                            resolve();
                        }
                        else {
                            this.xrButton.innerHTML = 'AR not found';
                            reject("AR not supported for this browser");
                        }
                        this.xrButton.disabled = !supported; // Enable/disable the button
                    });
                }
                else {
                    reject("WebXR unavailable for this browser");
                }
            });
        };
        // Same function as above but when the device changes? Also not sure if it's necessary...
        this.checkSupportedState = () => {
            navigator.xr.isSessionSupported('immersive-ar').then((supported) => {
                if (supported) {
                    this.xrButton.innerHTML = 'Exit App';
                    this.xrButton.addEventListener('click', this.enterAr);
                }
                else {
                    this.xrButton.innerHTML = 'AR not found';
                }
                this.xrButton.disabled = !supported;
            });
        };
        // Start or end the XRSession
        this.enterAr = () => {
            // Case 1: There is no running active sesson
            if (!this.xrSession) {
                this.app.geoalignment.updateGeolocation(); // Calculate the distances new, in case the location changed
                navigator.xr.requestSession('immersive-ar', {
                    optionalFeatures: ['dom-overlay'],
                    requiredFeatures: ['local', 'hit-test'],
                    domOverlay: { root: document.getElementById('home') } // Use the whole interface as overlay, some elements are see-through, so the session is visible in the background
                }).then(this.onSessionStarted, this.onRequestSessionError);
            }
            // Case 2: There is already an active session
            else {
                this.xrSession.end();
            }
        };
        // Case 1 (Success) from enterAR()
        this.onSessionStarted = (session) => {
            this.xrSession = session;
            session.addEventListener('end', this.onSessionEnded); // In case the session is ended, end it properly
            // initialize three.js scene
            this.app.threeScene.initScene(session);
            // Place all the objects
            this.app.threeScene.placeBalloons2();
            // Not sure if I need this next one, was giving problems with ts so I commented it and it still works
            //session.updateRenderState({ baseLayer: new XRWebGLLayer(session, gl) });
            // This reference space is only needed if we need to detect the flooe and stuff
            session.requestReferenceSpace('viewer').then((refSpace) => {
                session.requestHitTestSource({ space: refSpace }).then((hitTestSource) => {
                    this.xrHitTestSource = hitTestSource;
                });
            });
            // This reference space is needed for positioning stuff in the world
            session.requestReferenceSpace('local').then((refSpace) => {
                this.xrRefSpace = refSpace;
                session.requestAnimationFrame(this.onXRFrame); // Here start the animation!
            });
        };
        // Case 1 (Error) from enterAR()
        this.onRequestSessionError = (ex) => {
            console.error(ex.message);
        };
        // Case 2 from enterAR(): Shut down the session properly
        this.onSessionEnded = (event) => {
            this.xrSession = null;
            this.app.threeScene.gl = null; // Remove the gl layer
            if (this.xrHitTestSource)
                this.xrHitTestSource.cancel(); // Cancel hit testing
            this.xrHitTestSource = null;
        };
        // Update function, is called for every frame!
        this.onXRFrame = (t, frame) => {
            let session = frame.session; // Get the current session
            this.app.threeScene.checkTurning();
            session.requestAnimationFrame(this.onXRFrame); // Already request the next frame, so it keeps going
            // bind the gl context that was created with WebXR to threejs renderer
            this.app.threeScene.gl.bindFramebuffer(this.app.threeScene.gl.FRAMEBUFFER, session.renderState.baseLayer.framebuffer);
            // Retrieve the pose of the device.
            // XRFrame.getViewerPose can return null while the session attempts to establish tracking.
            const pose = frame.getViewerPose(this.xrRefSpace);
            if (pose) {
                // In mobile AR, we only have one view.
                const view = pose.views[0];
                // Use the view's transform matrix and projection matrix to configure the THREE.camera.
                this.app.threeScene.camera.matrix.fromArray(view.transform.matrix);
                this.app.threeScene.camera.projectionMatrix.fromArray(view.projectionMatrix);
                this.app.threeScene.camera.updateMatrixWorld(true);
                // GEOALIGNMENT: Turn the coordinate system of the THREE.Scene to match the global geo coordinate system
                var vecCamera = new THREE.Vector3(); // Prepare a empty vector
                this.app.threeScene.camera.getWorldDirection(vecCamera); // Get the orientation of the camera
                this.orientLocal = Math.atan2(vecCamera.z, vecCamera.x) * 180 / Math.PI + 180; // Tricky part: Calculate the angle of the orientation but only on the xz plane (horizontal)
                this.orientLocal = (this.orientLocal + 270) % 360; // Make it match the global one, turn and make it between 0 and 360
                var diff = this.app.geoalignment.orientGlobal - this.orientLocal; // Get the difference between the local and global orientation
                // The tricky part: Check if this difference is still close to the difference that was applied last time. 
                // But only do sth if the value is above a certain threshold, otherwise there is a lot of jitter
                // Here the threshold was set to 1 degree
                if (Math.abs(diff - this.helper.toDegrees(this.app.threeScene.scene.rotation.y)) > 1) {
                    this.app.threeScene.scene.rotation.y = this.helper.toRadians(diff); // Turn the whole scene so that it matches the global one!
                }
                // Write it to the dev infos
                this.diffOrientVis.innerHTML = "Difference: " + diff.toFixed(0).toString();
                this.orientLocalVis.innerHTML = "Local orientation: " + this.orientLocal.toFixed(0).toString();
                var vecCameraPos = new THREE.Vector3(); // Prepare a empty vector
                this.app.threeScene.camera.getWorldPosition(vecCameraPos);
                this.localLocVis.innerHTML = "Local Position: " + vecCameraPos.x.toFixed(2).toString() + ", " + vecCameraPos.y.toFixed(2).toString() + ", " + vecCameraPos.z.toFixed(2).toString();
                this.positionLocal = [vecCameraPos.x, vecCameraPos.y, vecCameraPos.z];
                this.app.threeScene.updateCompassArrows(this.app.btnDevMode.checked);
                if (this.helper.distanceCart(this.positionLocal, this.previousPositionLocal) > 1) {
                    this.app.threeScene.placeBalloons2();
                    this.previousPositionLocal = this.positionLocal;
                    this.helper.log("Balloons updated!");
                }
                // Only use the next part for floor detecion
                /*
                if (xrHitTestSource) {
                // obtain hit test results by casting a ray from the center of device screen
                // into AR view. Results indicate that ray intersected with one or more detected surfaces
                const hitTestResults = frame.getHitTestResults(xrHitTestSource);
                if (hitTestResults.length) {
                    // obtain a local pose at the intersection point
                    const pose = hitTestResults[0].getPose(xrRefSpace);
                    // place a reticle at the intersection point
                    reticle.matrix.fromArray(pose.transform.matrix);
                    reticle.visible = true;
                }
                } else {  // do not show a reticle if no surfaces are intersected
                reticle.visible = false;
                }
                */
                // Render the scene with THREE.WebGLRenderer.
                this.app.threeScene.renderer.render(this.app.threeScene.scene, this.app.threeScene.camera);
            }
        };
        this.app = app;
        this.helper = new Helper();
    }
}
