/*
--------------
Geoalignment.ts
--------------
Here all the information and functions about compass and geo location is gathered. First it
handles the permission of the orientation (compass) and the geo location. Then there are the listeners
which fire every time the orientation or location changes.
*/
import { Helper } from './Helper.js';
//import {geolocate} from "mock-geolocation"; // used to fake the geolocation
import proj4 from 'proj4';
if (typeof window !== 'undefined') {
    window.proj4 = window.proj4 || proj4;
}
export default proj4;
export class Geoalignment {
    constructor(app) {
        this.orientGlobal = null; // Stores the current orientation of the phone in the global coordinate system (degree). The global one is stored in ARHandler.ts
        this.positionGlobal = [0, 0, 0]; // Stores the current geo location
        this.positionProj = [0, 0];
        this.btnFakeGeo = document.getElementById("fakeGeo"); // Switch in settings which changes the geo location to on in the middle of the city
        this.notifications = document.getElementById("notifications"); // Switch in settings which changes the geo location to on in the middle of the city
        this.notification = document.getElementById("notification"); // Switch in settings which changes the geo location to on in the middle of the city
        // HTML elements to write developer info to, only shown when in developer mode
        this.geoLocVis = document.getElementById('geoLocVis');
        this.geoLocVisProj = document.getElementById('geoLocVisProj');
        this.orientLocalVis = document.getElementById('orientLocalVis');
        this.orientGlobalVis = document.getElementById('orientGlobalVis');
        this.diffOrientVis = document.getElementById('diffOrientVis');
        this.init = () => {
            return new Promise((resolve, reject) => {
                this.permissionGeo((response) => {
                    if (response) {
                        this.permissionCompass((response) => {
                            if (response) {
                                resolve(); // When all is loaded, remove splash screen and show home container
                            }
                            else {
                                reject("Permission for Compass not available");
                            }
                        });
                    }
                    else {
                        reject("Permission for Geolocation not available");
                    }
                });
            });
        };
        // First Part: Geolocatiion
        //Get the permission to read the location 
        this.permissionGeo = (callback) => {
            // First check if it is even possible
            if (!navigator.geolocation) {
                alert("Geolocation not supported.");
                callback(false);
            }
            else {
                //(navigator as any).geolocation.getCurrentPosition(handlerGeo);  // Only get one location
                //geolocate.use();
                navigator.geolocation.watchPosition(this.handlerGeo, this.errorGeo); // Keep track of the locatiom (fires when the locaton changes)
                /*
                geolocate.send({
                  latitude: 50,
                  longitude: 10,
                  accuracy: 5,
                  timestamp: 3000
                });
                */
                // Call back to say everthing worked
                callback(true);
            }
        };
        // Handle the new position
        this.handlerGeo = (pos) => {
            // Reduce computational effort, only update when necessary (when difference more then 1m)
            if (this.helper.distance(this.positionGlobal[1], this.positionGlobal[0], pos.coords.latitude, pos.coords.longitude, "K") > 0.001) {
                this.positionGlobal = [pos.coords.longitude, pos.coords.latitude, pos.coords.accuracy]; // Write it to the global variable
                // Write geolocation to dev info
                proj4.defs('CH1904P', "+proj=somerc +lat_0=46.95240555555556 +lon_0=7.439583333333333 +k_0=1 +x_0=2600000 +y_0=1200000 +ellps=bessel +towgs84=674.374,15.056,405.346,0,0,0,0 +units=m +no_defs ");
                this.positionProj = proj4("CH1904P", [this.positionGlobal[0], this.positionGlobal[1]]);
                this.geoLocVis.innerHTML = "Global Position: " + this.positionGlobal[0].toFixed(6).toString() + ", " + this.positionGlobal[1].toFixed(6).toString() + ", acc: " + this.positionGlobal[2].toFixed(1).toString() + "m";
                this.geoLocVisProj.innerHTML = "Global Position: " + this.positionProj[0].toFixed(2).toString() + ", " + this.positionProj[1].toFixed(2).toString() + ", acc: " + this.positionGlobal[2].toFixed(1).toString() + "m";
                this.updateGeolocation(); // Recalculate the distances and bearings to the different landmarks
            }
        };
        // In case the getting of the location does not work
        this.errorGeo = (err) => {
            console.warn('ERROR(' + err.code + '): ' + err.message);
        };
        // Update the distances and bearings for each landmark point
        this.updateGeolocation = () => {
            for (var i in this.app.pointData) {
                this.app.pointData[i].updateDist(this.getPosition());
                if (this.notifications.checked && this.app.activeContainer == this.app.walkContainer) {
                    if (this.app.pointData[i].distance < 50) {
                        if (!this.app.pointData[i].closeby) {
                            var landmarkID = i;
                            document.getElementById("notificationInfo").innerHTML = "You are close to the landmark " + this.app.pointData[i].name + ".<br> Do you want to check it out?";
                            this.notification.style.display = "block";
                            document.getElementById("btnNotiYes").addEventListener('click', () => { console.log(this.app.pointData[landmarkID].name); this.app.showModal(parseInt(landmarkID)); this.notification.style.display = "none"; });
                            document.getElementById("btnNotiNo").addEventListener('click', () => { this.notification.style.display = "none"; });
                        }
                    }
                    this.app.pointData[i].closeby = true;
                }
                else {
                    this.app.pointData[i].closeby = false;
                }
            }
            this.helper.log("Distances updated!");
        };
        // Read the position from outside of the class, also lets you fake the location
        this.getPosition = () => {
            if (this.btnFakeGeo.checked) {
                return [8.542248, 47.371773, 0];
            }
            else {
                return this.positionGlobal;
            }
        };
        // Second part: compass information
        // Get the permission to use the orientation (actually only needed in iOS)
        this.permissionCompass = (callback) => {
            // in iOS 13 we need to ask for permission to get the compass info, in android not
            if (this.helper.iOS()) {
                // Not sure what this is for, maybe case of iOS but lower than verison 13?
                if (typeof (DeviceMotionEvent) !== "undefined" && typeof (DeviceMotionEvent.requestPermission) === "function") {
                    // Ask for permisison
                    DeviceMotionEvent.requestPermission()
                        .then(response => {
                        if (response == "granted") {
                            this.checkHandlerCompass(callback); // Handle the info
                        }
                    })
                        .catch(console.error);
                }
                else {
                    this.helper.log("DeviceMotionEvent is not defined");
                    this.checkHandlerCompass(callback); // Handle the info nevertheless (?)
                }
            }
            // Case android, go directly to the handler
            else {
                this.checkHandlerCompass(callback); // Handle the info
            }
        };
        // Deal with the info from the orientation (compass)
        this.checkHandlerCompass = (callback) => {
            // Check if device can provide absolute orientation data
            // There is no real standard between the devices and OS. There are three different listeners, 
            // we just try all of them
            // This is used in the case of iOS (I think) TODO!
            if (window.DeviceOrientationAbsoluteEvent) {
                this.helper.log("Using window.DeviceOrientationAbsoluteEvent");
                window.addEventListener("DeviceOrientationAbsoluteEvent", this.handlerCompass);
                callback(true);
            }
            // Since android changed to provide relative data with the "standard" listener, there is this non-standard one which also does not work for all devices
            else if ('ondeviceorientationabsolute' in window) {
                // We can listen for the new deviceorientationabsolute event.
                this.helper.log("Using ondeviceorientationabsolute");
                window.addEventListener("deviceorientationabsolute", this.handlerCompass, true);
                callback(true);
            }
            // This is the standard one which mostly only provides relativ values, at least for android
            // TODO: Check this and give an alert!
            else if (window.DeviceOrientationEvent) {
                alert("Only relative orientation existing");
                window.addEventListener("deviceorientation", this.handlerCompass);
                callback(true);
            } // Send an alert if the device isn't compatible
            else {
                alert("Sorry, try again on a compatible mobile device!");
                callback(false);
            }
        };
        // Deal with the orientation data
        this.handlerCompass = (event) => {
            // There are three values for the orientation in the three axis
            var alpha = event.alpha; //z axis rotation [0,360)
            var beta = event.beta; //x axis rotation [-180, 180]
            var gamma = event.gamma; //y axis rotation [-90, 90]
            // This is what we want in the end, the heading/azimuth/compass info
            var heading = null;
            // In the case of iOS there is also an accuracy
            let acc = null;
            //Case iOS, use webkit
            if (typeof event.webkitCompassHeading !== "undefined") {
                heading = event.webkitCompassHeading; //iOS non-standard
                acc = event.webkitCompassAccuracy;
            }
            // All other cases, use a helper function to get the compass info from all three angle values
            else {
                heading = this.helper.compassHeading(alpha, beta, gamma);
            }
            // Reduce computational effort, only update when necessary (when differenc is larger than 0.1 degree)
            if (Math.abs(heading - this.orientGlobal) > 0.1) {
                // Write to dev info
                if (acc == null) {
                    this.orientGlobalVis.innerHTML = "Global orientation: " + heading.toFixed(0).toString();
                }
                else if (acc == -1) {
                    this.orientGlobalVis.innerHTML = "Global orientation: Not calibrated - not usable";
                }
                else {
                    this.orientGlobalVis.innerHTML = "Global orientation: " + heading.toFixed(0).toString() + " +/- " + acc.toFixed(0).toString();
                }
                // Update local compass info (class variable)
                this.orientGlobal = heading;
                // This part is to detect if we look close to to one of the landmarks
                /*
                if (this.app.pointData != null) {
                  let diff: number = 400;
                  for (var i in this.app.pointData) {
                    // TODO: overlauf nach 360!!!
                    if (Math.abs(this.orientGlobal - this.app.pointData[i].bearing) < diff) {
                      diff = Math.abs(this.orientGlobal - this.app.pointData[i].bearing);
                    }
                  }
                  
                  if ((this.pointVis.innerHTML != "Loading...") && (this.pointVis.innerHTML.slice(0,5) != "Model")) {
                    if (diff < 10) {
                   
                      this.pointVis.innerHTML = this.app.pointData[clostestOrient].name + " (" + this.app.pointData[clostestOrient].distance.toFixed(0) + "m)";
                    } else {
                      this.pointVis.innerHTML = "";
                    }
                  }
                }
                */
            }
        };
        this.app = app;
        this.helper = new Helper();
    }
}
